import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertController, ModalController } from '@ionic/angular';
import { Socket } from 'ngx-socket-io';
import { Observable } from 'rxjs';
import { InicioService } from 'src/app/providers/Almacenamiento/inicio.service';
import { TicketsService } from 'src/app/providers/Api/Tickets.service';
import { ToastServices } from 'src/app/providers/Utilidades/ToastService.service';
import { FechasModalComponent } from '../../../Components/fechas-modal/fechas-modal.component';

@Component({
  selector: 'app-tickets-sala',
  templateUrl: './tickets-sala.page.html',
  styleUrls: ['./tickets-sala.page.scss'],
})
export class TicketsSalaPage implements OnInit {

  configurando = false;
  usuario = '';
  habilitado = false;
  cargado = false;
  id = '';

  tickets = [];

  constructor(
    private almacenamiento: InicioService,
    private toast: ToastServices,
    private apiTickets: TicketsService,
    private alert: AlertController,
    private router: Router,
    private socket: Socket,
    private modal: ModalController,
    private active: ActivatedRoute
  ) { 
    this.escucharMensajes().subscribe((data: any) => {
      // console.log(data, ' data suscribe')
      if (data.msg) {
        this.tickets = this.tickets.filter((item) => {
          return item._id !== data.id;
        })
        this.toast.toastExitoso('Ticket rechazado');
      } else {
        this.toast.toastError('No se pudo rechazar el ticket, inténtelo más tarde');
      }

    })

    this.escucharRechazos().subscribe((data: any) => {
      if (data.exe) {
        // console.log(data, 'sus')
       

        this.tickets = this.tickets.filter((item) => {
          return item._id !== data.data._id;
        })
      } else {
        this.toast.toastError('No se pudo rechazar tu ticket');
      }
    })



    

    this.escucharCreados().subscribe((data: any) => {
      // console.log(data, ' data suscribe')
      if (data.exe) {
        this.tickets.push(data.data)
      } 
    })
  }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.misTickets();
    this.almacenamiento.insertarTicket('entrando').then((r) => {
      if (r) {
        return true;
      }
    });
  }

  refrescando(event) {
    this.tickets = [];
    this.misTickets().then(() => {
        event.detail.complete();
    }).catch(() => {
      event.detail.complete();
    })
  }

  async misTickets() {
    this.configurando = true;
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.apiTickets.misTicketsLista(info.token, {id: info._id}).subscribe((data: any) => {
        if (data.exe) {
          this.tickets = data.response;
          this.tickets.forEach((item) => {
            this.socket.emit('ticket', {id: item.negocio._id} );
          })
        } else {
          this.toast.toastError('No se pudo traer las tickets, inténtelo más tarde');
          this.router.navigate(['/central/menu/'])
        }
        this.configurando = false;
        this.cargado = true;
      }, (err) => {
        this.toast.toastError('No se pudo traer las tickets, inténtelo más tarde');
        this.router.navigate(['/central/menu/'])
        this.configurando = false;
        this.cargado = true;
      })
    }
  }

  async rechazar(ticket) {
      const info = await this.almacenamiento.obtenerToken();
  
      if ( info ) {
        this.socket.emit('rechazar', {idtaller: ticket.negocio._id, id: ticket._id, idusuario: ticket.creador._id} );
      }
  }

  escucharMensajes() {
    const myobserver = new Observable(observer => {
      this.socket.on('mensajeTicket', (data) => {
        observer.next(data);
      });
    });
  
    return myobserver;
  }

  escucharCreados() {
    const myobserver = new Observable(observer => {
      this.socket.on('crear', (data) => {
        observer.next(data);
      });
    });
  
    return myobserver;
  }

  async miModal(ticket) {
    const modal = await this.modal.create({
      component: FechasModalComponent
    })

    await modal.present();

    const data = await modal.onWillDismiss();

    if ( data ) {
        if (data.data) {
          const info = await this.almacenamiento.obtenerToken();
  
          if ( info ) {
            // tslint:disable-next-line:max-line-length
            this.socket.emit('procesar', {idtaller: ticket.negocio._id, id: ticket._id, idusuario: ticket.creador._id, sala: this.active.snapshot.paramMap.get('sala'), fecha1: data.data.fecha1, fecha2: data.data.fecha2} );
            this.alerta(ticket.creador);
          }
        }
    }
  }


  
  async alerta(data) {
    const alert = await this.alert.create({
      header: 'Crear hoja de vida para este ticket',
      message: 'Crear la hoja de vida para registrar el trabajo realizado en este ticket',
      buttons: [
        {
          text: 'No quiero',
          cssClass: 'cancelarAlerta',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Crear',
          cssClass: 'botonalerta',
          handler: () => {
            this.crearhoja(data);
          }
        }
      ]
    })

    await alert.present();
  }

  async crearhoja(data) {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
    
      this.apiTickets.crearHoja(info.token, {idusuario: data._id, placa: data.placa}).subscribe((res: any) => {
        if (res.exe) {
          this.toast.toastExitoso('Hoja de vida creada correctamente');
        } else {
          this.toast.toastError('No se pudo crear la hoja de vida, inténtelo más tarde');
          this.router.navigate(['/central/menu/'])
        }
      }, (err) => {
        this.toast.toastError('No se pudo crear la hoja de vida, inténtelo más tarde');
        this.router.navigate(['/central/menu/'])
      })
  }

}

escucharRechazos() {
  const myobserver = new Observable(observer => {
    this.socket.on('rechazar', (data) => {
      observer.next(data);
    });
  });

  return myobserver;
}


  
async navegar(id) {
  const info = await this.almacenamiento.obtenerToken();

  if ( info ) {

      this.apiTickets.consultarSala(info.token, {ticket: id}).subscribe((data: any) => {
       //  console.log(data)
        if (data.exe) {
          if (data.response.length > 0) {
            if (data.response[0].accesos.length > 0) {
              this.almacenamiento.insertarChat(data.response[0]).then(() => {
                this.router.navigate(['/central/chats/chat']);
               
              }).catch(() => {
                this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
              })
            } else {
              this.toast.Especial('El ticket fue cancelado, ya no puede acceder al chat');
            }
          } else {
            this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
          }
          
        } else {
          this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
         
        }
      }, (err) => {
        this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
      })
    }


 
}

  ionViewWillLeave() {
    this.almacenamiento.eleminarTicket().then((r) => {
      if (r) {
        return true;
      }
    });
  }

}
